import React, { Component } from 'react';
import './dialogbox.css';

class Dialogbox extends React.Component {
    style = { 
        width:'250px',
        height:'200px',
        backgroundColor:'#0008',
        textAlign:'center',
        padding:'10px',
        marginLeft:'60px',
        borderRadius:'5px',
        transform:'translateY(-180%)'
     }
    render() { 
        return ( 
            <div style={this.style} className={this.props.display}>
                <h2 className="color mt-5">Game Over</h2>
                <button className="btn btn-light mdbutton mt-2" onClick={() =>{this.props.continue()}}>Continue</button>
            </div>
         );
    }
}
 
export default Dialogbox;