import React, { Component } from 'react';

class Reset extends React.Component {
     style ={
       borderRadius:'50px',
       width:'330px'
     }
    render() { 
        return ( 
            <button style={this.style} className="btn btn-outline-secondary btn-lg m-3" onClick={()=>{this.props.resetGame()}}>Start a new game</button>
         );
    }
}
 
export default Reset;