import React from 'react';
import Button from './components/btn';
import Reset from './components/resetgame';
import Status from './components/status';
import Heading from './heading';
import O from './components/o';
import X from './components/x';
import Dialogbox from './components/dialog';

class App extends React.Component {
  state = { 
    x:0,
    xcolor:'',
    ocolor:'o-background',
    heading:"Let's play Tic-Tac-Toe",
    sign:[' ',' ',' ',' ',' ',' ',' ',' ',' '],
    notation:[<O /> , <X />],
    xwin:0,
    owin:0,
    display:'none'
   }
   choose =(id) =>{
    let arr = this.state.sign;
    
    if(this.state.x%2 ===0 && arr[id] ===' '){
      arr[id] = this.state.notation[0]; 
      this.setState({
        sign:arr,
        x:this.state.x+1,
        xcolor:'x-background',
        ocolor:''
      })
    }
    
    if(this.state.x%2 !==0 && arr[id] ===' '){
      arr[id]=this.state.notation[1];
      this.setState({
        sign:arr,
        x:this.state.x+1,
        ocolor:'o-background',
        xcolor:''
      })
    }
    
    for(let i = 0 ; i < this.state.notation.length ;i++){
     if((this.state.sign[0] === this.state.notation[i] && this.state.sign[1] === this.state.notation[i] && this.state.sign[2] === this.state.notation[i]) || 
        (this.state.sign[3] === this.state.notation[i] && this.state.sign[4] === this.state.notation[i] && this.state.sign[5] === this.state.notation[i]) ||
        (this.state.sign[6] === this.state.notation[i] && this.state.sign[7] === this.state.notation[i] && this.state.sign[8] === this.state.notation[i]) || 
        (this.state.sign[i] === this.state.notation[i] && this.state.sign[3] === this.state.notation[i] && this.state.sign[6] === this.state.notation[i]) ||
        (this.state.sign[1] === this.state.notation[i] && this.state.sign[4] === this.state.notation[i] && this.state.sign[7] === this.state.notation[i]) || 
        (this.state.sign[2] === this.state.notation[i] && this.state.sign[5] === this.state.notation[i] && this.state.sign[8] === this.state.notation[i]) ||
        (this.state.sign[i] === this.state.notation[i] && this.state.sign[4] === this.state.notation[i] && this.state.sign[8] === this.state.notation[i]) || 
        (this.state.sign[2] === this.state.notation[i] && this.state.sign[4] === this.state.notation[i] && this.state.sign[6] === this.state.notation[i]) 
     ){
       if(i===0){
         this.setState({heading:"Player O won the game...", owin:this.state.owin+1,display:''})
       }
       if(i===1){
        this.setState({heading:"Player X won the game...", xwin:this.state.xwin+1,display:''})
       }
      }
    }

    let a = 0;
    for(let i = 0 ; i <this.state.sign.length ;i++){
      if(this.state.sign[i]!== this.state.notation[0] && this.state.sign[i]!== this.state.notation[1] ){
        a++;
      }
    }
    if(a===0){
      this.setState({heading:"Opsss,,,,, It is Tie...",display:''})
    }

  }
   resetGame = () =>{
     var  emp = [' ',' ',' ',' ',' ',' ',' ',' ',' '];
     this.setState({sign:emp,ocolor:'o-background',xcolor:'',heading:"Let's play Tic-Tac-Toe",x:0,xwin:0,owin:0})
   }  
   continue = () =>{
    var  emp = [' ',' ',' ',' ',' ',' ',' ',' ',' '];
    this.setState({sign:emp,ocolor:'o-background',xcolor:'',heading:"Let's play Tic-Tac-Toe",x:0,display:'none'})
  
   }
  render() { 
    return ( 
      
      <div className="d-flex justify-content-center mt-5">
      <div className="d-block">
      <div><Heading heading={this.state.heading} /></div><br />
      <div><Status xcolor={this.state.xcolor} ocolor={this.state.ocolor} xwin={this.state.xwin} owin={this.state.owin}/></div>
      <div>
         <Button id={0} choose={this.choose} name={this.state.sign[0]} />
         <Button id={1} choose={this.choose} name={this.state.sign[1]} />
         <Button id={2} choose={this.choose} name={this.state.sign[2]} />
       </div>
       <div>
         <Button id={3} choose={this.choose} name={this.state.sign[3]} />
         <Button id={4} choose={this.choose} name={this.state.sign[4]} />
         <Button id={5} choose={this.choose} name={this.state.sign[5]} />
       </div>
       <div>
         <Button id={6} choose={this.choose} name={this.state.sign[6]} />
         <Button id={7} choose={this.choose} name={this.state.sign[7]} />
         <Button id={8} choose={this.choose} name={this.state.sign[8]} />
       </div>
       <div><Reset resetGame={this.resetGame}/></div>
       <div><Dialogbox display={this.state.display} continue={this.continue} /></div>
       </div>
    
    </div>
       
     );
  }
}
 
export default App;