import React, { Component } from 'react';
import './status.css';
 class Status extends Component {
   state = {  }
   render() { 
     return ( 
       <div className="m-2">
         <div className="d-flex justify-content-between">
           <div>
               <p className="player">PLAYER  X</p>
               <h3 style={{color:'#fff'}}>{this.props.xwin}</h3>
           </div>
           <div className="d-flex mt-1">
               <div style={{border:'2px solid rgb(196, 80, 51)'}} className="outer-circle"><div style={{width:'12px',height:'12px',borderRadius:'50%'}} className={this.props.xcolor}></div></div>&nbsp;&nbsp;
               <div style={{border:'2px solid rgb(196, 51, 119)'}} className="outer-circle"><div style={{width:'12px',height:'12px',borderRadius:'50%'}} className={this.props.ocolor}></div></div>
           </div>
           <div>
           <p className="player">PLAYER  O</p>
               <h3 style={{color:'#fff',float:'right'}}>{this.props.owin}</h3>
           </div>
         </div>
       </div>
      );
   }
 }
  
 export default Status;